.footer {
  background: deepskyblue;
  width: 100px;
  height: 100px;
}
.header {
  display: flex;
  background: lightgrey;
  height: 70px;
}
.header__title {
  display: flex;
  width: 100%;
  justify-content: center;
}
.header__title h1 {
  color: #373a3c;
}
@import (once) '../variables';
.unorderd-list {
  display: flex;
  list-style-type: none;
  flex-direction: column;
}
.unorderd-list__item {
  border: 1px solid lightgrey;
  display: flex;
  width: 20rem;
  margin: 20px;
}
.unorderd-list__item-link {
  display: flex;
  padding: 20px;
  color: black;
  text-decoration: none;
}
html,
body {
  margin: 0;
}
